import React from 'react'
import img2home from "../images/img2home.png"

function Info2() {
    return (
        <div className="gradiente_verde row my-5 justify-content-center container-fluid">
            <div className="row">
                <div className="col-md-5">
                    <img src={img2home} alt="Descripcin de la imagen" className='imginfo mt-3' />
                </div>
                <div className="col-md-5">
                    <div>
                        <h2 className='text-start display-3 fw-bold quicksand titulo mb-5'> <span className='morado'>Optimiza tu proceso</span> de pedidos con nuestra plataforma en linea</h2>
                        <p className='text-start cuerpo'>Nuestra interfaz rpida e intuitiva permite agilizar la<br/> generacin y recepcin de ordenes de compra.<br/> Adaptamos la tecnologa al trabajo de cocineros,<br/> administradores y vendedores.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Info2